.note-card {
	position: relative;
	display: flex;
	margin-inline: 1rem;
	margin-top: 1.5rem;
	padding: 1rem 0.5rem 1rem 1rem;
	border-radius: 5px;
	border-bottom-left-radius: 25px;
	color: var(--clr-main-300);
	background-color: var(--clr-accent-dark);
	box-shadow: 10px 10px 20px -5px rgba(0, 0, 0, 0.25),
		5px 5px 20px -5px rgba(0, 0, 0, 0.5),
		2px 2px 5px 0px rgba(0, 0, 0, 0.25);
}


.note-card p {
	flex-grow: 1;
	max-width: 80%;
}

.note-card p.no-edit {
	max-width: 100%;
}

.note-card p {
	overflow-wrap: normal;
	margin-bottom: 0.5rem;
	font-size: 1.175rem;
	font-weight: 700;
}

.note-card div {
	display: flex;
	position: absolute;
	width: calc(20% - 2rem);
	top: 1rem;
	right: 1rem;
}

